import React from "react";

class MessagePanel extends React.Component {
  constructor(props) {
    super(props)
    this.state = {};

    this.onClose = this.onClose.bind(this);
  }

  componentDidMount() {

  }

  onClose = () => {
    this.props.handleCloseMessage(this.props.id);
  };
  
  render() {
    var animationDelay = 5 + 0.5*((this.props.id)) + "s";

    return (
      <div className="panel system-messages__message"
           style={{ animationDelay: animationDelay }}>
        <div className="panel__bar">
          <span className="panel__bar__title">{this.props.message.title}</span>
          <div className="panel__bar__close" onClick={this.onClose}>X</div>
        </div>
        
        <div className="panel__contents">{this.props.message.message}</div>
      </div>
    )
  }
}

export default MessagePanel;