import React, { Component} from "react";
import $ from "jquery";
import "./App.css";

import "./style/main.css";

import PanelGrid    from "./components/PanelGrid.jsx"
import MessagePanel from "./components/MessagePanel.jsx"

class App extends Component{
  constructor(props) {
    super(props);
    this.state = {
      editingEnabled: false,
      messages: [
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
        { title: "Warning", message: "Your copy of the S.O.L.O. Operating System seems to be unliscensed."},
      ],
      doneLoading: false,
    }

    this.handleEditButton = this.handleEditButton.bind(this)
    this.handleSendMessage = this.handleSendMessage.bind(this)
    this.handleCloseMessage = this.handleCloseMessage.bind(this)
  }
  
  componentDidMount() {
    this.setState({
      doneLoading: true,
    }, () => {
      $(".system-messages__message").addClass("slide-in");
    });
  }

  handleEditButton() {
    this.setState({
      editingEnabled: !this.state.editingEnabled,
    })
  }

  handleSendMessage(title, message) {
    var messages = this.state.messages.concat([]);
    messages.push({ title: title, message: message });
    this.setState({
      messages: messages,
    });
  }

  handleCloseMessage(key) {
    var messages = this.state.messages.concat([]);
    
    if(key === "all") messages = [];
    else              messages.splice(key, 1);
    this.setState({
      messages: messages,
    });
  }

  render(){
    // var state = this.state;
    var handleCloseMessage = this.handleCloseMessage;

    return(
      <div className="container">
        <header className="header">
          <h1>S.O.L.O. Operating System</h1>
          <button className="button button--inverted" onClick={this.handleEditButton}>
            {  this.state.editingEnabled && "Lock Layout" }
            { !this.state.editingEnabled && "Unlock Layout" }
          </button>
        </header>
        {
          this.state.doneLoading &&
            <ul className="system-messages">
              <button className="button" onClick={() => { handleCloseMessage("all") }}>Clear all</button>
              {
                this.state.messages.map((message, key) =>
                  <MessagePanel message={message} key={key} id={key}
                                handleCloseMessage={this.handleCloseMessage} />
                )
              }
            </ul>
        }
        
        <div className="grid-container">
          <PanelGrid editingEnabled={this.state.editingEnabled} />
        </div>
      </div>
    );
  }
}

export default App;